header {
    align-items: center;
    box-shadow: 0 0.25rem 0.75rem lightgrey;
    display: flex;
    justify-content: space-between;
}

header img,
header p {
    user-select: none;
}

header > div,
.header-logo {
    align-items: center;
    display: flex;
    gap: 0.5rem;
    margin: 0.5rem 1rem;
}

.header-logo > img {
    background: white;
    width: 32px;
}

.language-flag {
    cursor: pointer;
}