@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,400;0,800;1,400;1,800&display=swap');

/*
html {
  color-scheme: light dark;
}
*/

:root {
  --blue-300: #0c5eb0;
  --blue-500: #1cb0f6;
  --blue-700: #64ceff;
  --red-300: #b00c0c;
  --red-500: #f61c1c;
  --green-300: #199400;
  --green-500: #6acb56;
}

* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  font-weight: 400;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

body {
  font-size: 1.5rem;
}

h1 {
  font-size: 2rem;
  font-weight: 800;
}

h2 {
  font-weight: 800;
  margin: 1rem;
  text-align: center;
}

img {
  max-width: 100%;
}
