.storybook-article {
    margin-inline: 1rem;
}

.storybook-article img {
    pointer-events: none;
}

.storybook-page {
    display: flex;
    flex-direction: column;
    margin: 2rem auto;
    max-width: 512px;
}

.storybook-page-img {
    margin: 0 0 1rem;
}

.storybook-line {
    display: flex;
    margin-inline: 0.5rem;
    opacity: 1;
    transition: 250ms;
}

.character-icon {
    margin: 0.55rem 0.5rem auto 0;
    width: 2em;
}

.text-bubble {
    border: 2px solid lightgrey;
    border-radius: 1rem;
    padding: 1rem;
}

.text-no-bubble {
    padding: 1rem;
}

.btn-tray {
    display: flex;
    gap: 1rem;
    justify-content: center;
    opacity: 1;
    transition: 250ms;
}

.btn-next {
    align-items: center;
    background: var(--blue-500);
    border: 2px solid var(--blue-300);
    border-bottom-width: 4px;
    border-radius: 1rem;
    color: white;
    cursor: pointer;
    display: flex;
    font-family: 'Nunito', sans-serif;
    font-size: 4rem;
    padding: 0 4rem;
    position: relative;
}

.btn-next:hover {
    background: var(--blue-700);
}

.btn-next:active {
    border-bottom-width: 2px;
    top: 2px;
}

.btn-next.btn-img {
    padding: 1rem;
}

.btn-next img {
    background: white;
    border: 1rem solid white;
    border-radius: 50%;
}

.right-option {
    background: var(--green-500) !important;
    border-bottom-width: 4px !important;
    border-color: var(--green-300) !important;
    top: 0px !important;
}

.wrong-option,
.wrong-option:active,
.wrong-option:hover {
    background: var(--red-500) !important;
    border-bottom-width: 4px !important;
    border-color: var(--red-300) !important;
    top: 0px !important;
}

.lock {
    opacity: 0;
}

.lock * {
    pointer-events: none;
}